
@import url("https://fonts.googleapis.com/css2?family=Inter&family=Lato&family=Libre+Baskerville&family=Merriweather:wght@700&family=Work+Sans&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Work Sans", sans-serif;
}

html {
  color: rgb(96 101 123);
  font-size: 16px;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(255 255 255);
}

.container {
  padding: 1.875rem;
  margin-top: 1.5625rem;
  max-width: 100%; /* Ensure container doesn't exceed screen width */
}

h2 {
  color: rgb(43 45 56);
  text-align: center;
  font-size: 3rem;
  letter-spacing: -0.0125rem;
  font-family: "Merriweather", serif;
  margin-top: -3.1rem;
}

p {
  max-width: 50rem;
  text-align: center;
  margin-top: 0.125rem;
  margin-bottom: 2.125rem;
}

.main-content {
  width: 100%; /* Full width on small screens */
  min-height: 25rem;
  height: auto;
  padding: 1.125rem 1.125rem 7.5rem 1.125rem;
  position: relative;
  resize: none;
  box-shadow: 0 0.75rem 3rem 0px rgba(109 117 141 / 20%);
  background: rgb(255 255 255);
  border: 0.03125rem solid rgb(231 233 245);
  border-radius: 1rem;
}

.main-content:focus {
  border: 0.01875rem solid rgba(43, 45, 56, 0.3);
  outline: none;
}

p,
.main-content {
  font-size: 1.125rem;
  letter-spacing: 0.0625rem;
  word-spacing: 0.125rem;
  line-height: 1.6;
}

.btn-style {
  display: flex;
  margin-top: 0.625rem;
  gap: 3rem;
  justify-content: center;
  flex-wrap: wrap; 

}

button {
  background: rgb(17 166 131);
  color: rgb(255 255 255);
  border-radius: 0.375rem;
  padding: 1rem 2rem;
  border: none;
  font-size: 1.125rem;
  letter-spacing: 0.0625rem;
  margin: 0.075rem;
  display: flex;
  cursor: pointer;
}

button:focus,
button:hover {
  background: rgb(42, 201, 164);
  box-shadow: 0 0.3125rem 1.875rem 0 rgba(0, 0, 0, 0.1);
}

.counter-container {
  margin-top: 1.875rem;
  display: flex;
  justify-content: space-between;
}

.browserSupport {
  display: flex;
  color: red;
}

.grant {
  margin: 0;
  display: inline;
}

@media screen and (max-width: 48rem) {
  html {
    font-size: 14px;
  }
}
